import WebApiServer from '../../../../../../packages/art-lib-common/dist/core-server/WebApiServer';
export default class MainService extends WebApiServer {
    constructor() {
        super({
            baseURL: 'http://me.dev.com:8000'
        });
    }
    mainTestDto(result) {
        return JSON.parse(result);
    }
    mainTest() {
        return this.requestGet('mock_api/main/testme', {
            transformResponse: this.mainTestDto
        });
    }
}
